<template>
  <div>
    <!-- Èù¢ÂåÖÂ±ëÂØºËà™Âå∫Âüü -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">È¶ñÈ°µ</el-breadcrumb-item>
      <el-breadcrumb-item>ÊùÉÈôêÁÆ°ÁêÜ</el-breadcrumb-item>
      <el-breadcrumb-item>ËßíËâ≤ÂàóË°®</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- Âç°ÁâáËßÜÂõæ -->
    <el-card>
      <!-- Ê∑ªÂä†ËßíËâ≤ÊåâÈíÆÂå∫Âüü -->
      <el-row>
        <el-col>
          <el-button type="primary">Ê∑ªÂä†ËßíËâ≤</el-button>
        </el-col>
      </el-row>

      <!-- ËßíËâ≤ÂàóË°®Âå∫Âüü -->
      <el-table :data="rolelist" border stripe>
        <!-- Â±ïÂºÄÂàó -->
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-row :class="['bdbottom', i1 === 0 ? 'bdtop' : '', 'vcenter']" v-for="(item1, i1) in scope.row.children" :key="item1.id">
              <!-- Ê∏≤Êüì‰∏ÄÁ∫ßÊùÉÈôê -->
              <el-col :span="5">
                <el-tag closable @close="removeRightById(scope.row, item1.id)">{{item1.authName}}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!-- Ê∏≤Êüì‰∫åÁ∫ßÂíå‰∏âÁ∫ßÊùÉÈôê -->
              <el-col :span="19">
                <!-- ÈÄöËøá for Âæ™ÁéØ ÂµåÂ•óÊ∏≤Êüì‰∫åÁ∫ßÊùÉÈôê -->
                <el-row :class="[i2 === 0 ? '' : 'bdtop', 'vcenter']" v-for="(item2, i2) in item1.children" :key="item2.id">
                  <el-col :span="6">
                    <el-tag type="success" closable @close="removeRightById(scope.row, item2.id)">{{item2.authName}}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="18">
                    <el-tag type="warning" v-for="(item3, i3) in item2.children" :key="item3.id" closable @close="removeRightById(scope.row, item3.id)">{{item3.authName}}</el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>

            <!-- <pre>
              {{scope.row}}
            </pre> -->
          </template>
        </el-table-column>
        <!-- Á¥¢ÂºïÂàó -->
        <el-table-column type="index"></el-table-column>
        <el-table-column label="ËßíËâ≤ÂêçÁß∞" prop="roleName"></el-table-column>
        <el-table-column label="ËßíËâ≤ÊèèËø∞" prop="roleDesc"></el-table-column>
        <el-table-column label="Êìç‰Ωú" width="300px">
          <template slot-scope="scope">
            <el-button size="mini" type="primary" icon="el-icon-edit">ÁºñËæë</el-button>
            <el-button size="mini" type="danger" icon="el-icon-delete">Âà†Èô§</el-button>
            <el-button size="mini" type="warning" icon="el-icon-setting" @click="showSetRightDialog(scope.row)">ÂàÜÈÖçÊùÉÈôê</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <!-- ÂàÜÈÖçÊùÉÈôêÁöÑÂØπËØùÊ°Ü -->
    <el-dialog title="ÂàÜÈÖçÊùÉÈôê" :visible.sync="setRightDialogVisible" width="50%" @close="setRightDialogClosed">
      <!-- Ê†ëÂΩ¢Êéß‰ª∂ -->
      <el-tree :data="rightslist" :props="treeProps" show-checkbox node-key="id" default-expand-all :default-checked-keys="defKeys" ref="treeRef"></el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRightDialogVisible = false">Âèñ Ê∂à</el-button>
        <el-button type="primary" @click="allotRights">Á°Æ ÂÆö</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // ÊâÄÊúâËßíËâ≤ÂàóË°®Êï∞ÊçÆ
      rolelist: [],
      // ÊéßÂà∂ÂàÜÈÖçÊùÉÈôêÂØπËØùÊ°ÜÁöÑÊòæÁ§∫‰∏éÈöêËóè
      setRightDialogVisible: false,
      // ÊâÄÊúâÊùÉÈôêÁöÑÊï∞ÊçÆ
      rightslist: [],
      // Ê†ëÂΩ¢Êéß‰ª∂ÁöÑÂ±ûÊÄßÁªëÂÆöÂØπË±°
      treeProps: {
        label: 'authName',
        children: 'children'
      },
      // ÈªòËÆ§ÈÄâ‰∏≠ÁöÑËäÇÁÇπIdÂÄºÊï∞ÁªÑ
      defKeys: [],
      // ÂΩìÂâçÂç≥Â∞ÜÂàÜÈÖçÊùÉÈôêÁöÑËßíËâ≤id
      roleId: ''
    }
  },
  created() {
    this.getRolesList()
  },
  methods: {
    // Ëé∑ÂèñÊâÄÊúâËßíËâ≤ÁöÑÂàóË°®
    async getRolesList() {
      const { data: res } = await this.$http.get('roles')

      if (res.meta.status !== 200) {
        return this.$message.error('Ëé∑ÂèñËßíËâ≤ÂàóË°®Â§±Ë¥•ÔºÅ')
      }

      this.rolelist = res.data

      console.log(this.rolelist)
    },
    // Ê†πÊçÆIdÂà†Èô§ÂØπÂ∫îÁöÑÊùÉÈôê
    async removeRightById(role, rightId) {
      // ÂºπÊ°ÜÊèêÁ§∫Áî®Êà∑ÊòØÂê¶Ë¶ÅÂà†Èô§
      const confirmResult = await this.$confirm(
        'Ê≠§Êìç‰ΩúÂ∞ÜÊ∞∏‰πÖÂà†Èô§ËØ•Êñá‰ª∂, ÊòØÂê¶ÁªßÁª≠?',
        'ÊèêÁ§∫',
        {
          confirmButtonText: 'Á°ÆÂÆö',
          cancelButtonText: 'ÂèñÊ∂à',
          type: 'warning'
        }
      ).catch(err => err)

      if (confirmResult !== 'confirm') {
        return this.$message.info('ÂèñÊ∂à‰∫ÜÂà†Èô§ÔºÅ')
      }

      const { data: res } = await this.$http.delete(
        `roles/${role.id}/rights/${rightId}`
      )

      if (res.meta.status !== 200) {
        return this.$message.error('Âà†Èô§ÊùÉÈôêÂ§±Ë¥•ÔºÅ')
      }

      // this.getRolesList()
      role.children = res.data
    },
    // Â±ïÁ§∫ÂàÜÈÖçÊùÉÈôêÁöÑÂØπËØùÊ°Ü
    async showSetRightDialog(role) {
      this.roleId = role.id
      // Ëé∑ÂèñÊâÄÊúâÊùÉÈôêÁöÑÊï∞ÊçÆ
      const { data: res } = await this.$http.get('rights/tree')

      if (res.meta.status !== 200) {
        return this.$message.error('Ëé∑ÂèñÊùÉÈôêÊï∞ÊçÆÂ§±Ë¥•ÔºÅ')
      }

      // ÊääËé∑ÂèñÂà∞ÁöÑÊùÉÈôêÊï∞ÊçÆ‰øùÂ≠òÂà∞ data ‰∏≠
      this.rightslist = res.data
      console.log(this.rightslist)

      // ÈÄíÂΩíËé∑Âèñ‰∏âÁ∫ßËäÇÁÇπÁöÑId
      this.getLeafKeys(role, this.defKeys)

      this.setRightDialogVisible = true
    },
    // ÈÄöËøáÈÄíÂΩíÁöÑÂΩ¢ÂºèÔºåËé∑ÂèñËßíËâ≤‰∏ãÊâÄÊúâ‰∏âÁ∫ßÊùÉÈôêÁöÑidÔºåÂπ∂‰øùÂ≠òÂà∞ defKeys Êï∞ÁªÑ‰∏≠
    getLeafKeys(node, arr) {
      // Â¶ÇÊûúÂΩìÂâç node ËäÇÁÇπ‰∏çÂåÖÂê´ children Â±ûÊÄßÔºåÂàôÊòØ‰∏âÁ∫ßËäÇÁÇπ
      if (!node.children) {
        return arr.push(node.id)
      }

      node.children.forEach(item => this.getLeafKeys(item, arr))
    },
    // ÁõëÂê¨ÂàÜÈÖçÊùÉÈôêÂØπËØùÊ°ÜÁöÑÂÖ≥Èó≠‰∫ã‰ª∂
    setRightDialogClosed() {
      this.defKeys = []
    },
    // ÁÇπÂáª‰∏∫ËßíËâ≤ÂàÜÈÖçÊùÉÈôê
    async allotRights() {
      const keys = [
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedKeys()
      ]

      const idStr = keys.join(',')

      const { data: res } = await this.$http.post(
        `roles/${this.roleId}/rights`,
        { rids: idStr }
      )

      if (res.meta.status !== 200) {
        return this.$message.error('ÂàÜÈÖçÊùÉÈôêÂ§±Ë¥•ÔºÅ')
      }

      this.$message.success('ÂàÜÈÖçÊùÉÈôêÊàêÂäüÔºÅ')
      this.getRolesList()
      this.setRightDialogVisible = false
    }
  }
}
</script>

<style lang="less" scoped>
.el-tag {
  margin: 7px;
}

.bdtop {
  border-top: 1px solid #eee;
}

.bdbottom {
  border-bottom: 1px solid #eee;
}

.vcenter {
  display: flex;
  align-items: center;
}
</style>
