<template>
  <div>
    <!-- Èù¢ÂåÖÂ±ëÂØºËà™Âå∫Âüü -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">È¶ñÈ°µ</el-breadcrumb-item>
      <el-breadcrumb-item>ÂïÜÂìÅÁÆ°ÁêÜ</el-breadcrumb-item>
      <el-breadcrumb-item>Ê∑ªÂä†ÂïÜÂìÅ</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- Âç°ÁâáËßÜÂõæ -->
    <el-card>
      <!-- ÊèêÁ§∫Âå∫Âüü -->
      <el-alert title="Ê∑ªÂä†ÂïÜÂìÅ‰ø°ÊÅØ" type="info" center show-icon :closable="false">
      </el-alert>
      <!-- Ê≠•È™§Êù°Âå∫Âüü -->
      <el-steps :space="200" :active="activeIndex - 0" finish-status="success" align-center>
        <el-step title="Âü∫Êú¨‰ø°ÊÅØ"></el-step>
        <el-step title="ÂïÜÂìÅÂèÇÊï∞"></el-step>
        <el-step title="ÂïÜÂìÅÂ±ûÊÄß"></el-step>
        <el-step title="ÂïÜÂìÅÂõæÁâá"></el-step>
        <el-step title="ÂïÜÂìÅÂÜÖÂÆπ"></el-step>
        <el-step title="ÂÆåÊàê"></el-step>
      </el-steps>

      <!-- tabÊ†èÂå∫Âüü -->

      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px" label-position="top">
        <el-tabs v-model="activeIndex" :tab-position="'left'" :before-leave="beforeTabLeave" @tab-click="tabClicked">
          <el-tab-pane label="Âü∫Êú¨‰ø°ÊÅØ" name="0">
            <el-form-item label="ÂïÜÂìÅÂêçÁß∞" prop="goods_name">
              <el-input v-model="addForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="ÂïÜÂìÅ‰ª∑Ê†º" prop="goods_price">
              <el-input v-model="addForm.goods_price" type="number"></el-input>
            </el-form-item>
            <el-form-item label="ÂïÜÂìÅÈáçÈáè" prop="goods_weight">
              <el-input v-model="addForm.goods_weight" type="number"></el-input>
            </el-form-item>
            <el-form-item label="ÂïÜÂìÅÊï∞Èáè" prop="goods_number">
              <el-input v-model="addForm.goods_number" type="number"></el-input>
            </el-form-item>
            <el-form-item label="ÂïÜÂìÅÂàÜÁ±ª" prop="goods_cat">
              <el-cascader expand-trigger="hover" :options="catelist" :props="cateProps" v-model="addForm.goods_cat" @change="handleChange">
              </el-cascader>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="ÂïÜÂìÅÂèÇÊï∞" name="1">
            <!-- Ê∏≤ÊüìË°®ÂçïÁöÑItemÈ°π -->
            <el-form-item :label="item.attr_name" v-for="item in manyTableData" :key="item.attr_id">
              <!-- Â§çÈÄâÊ°ÜÁªÑ -->
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox :label="cb" v-for="(cb, i) in item.attr_vals" :key="i" border></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="ÂïÜÂìÅÂ±ûÊÄß" name="2">
            <el-form-item :label="item.attr_name" v-for="item in onlyTableData" :key="item.attr_id">
              <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="ÂïÜÂìÅÂõæÁâá" name="3">
            <!-- action Ë°®Á§∫ÂõæÁâáË¶Å‰∏ä‰º†Âà∞ÁöÑÂêéÂè∞APIÂú∞ÂùÄ -->
            <el-upload :action="uploadURL" :on-preview="handlePreview" :on-remove="handleRemove" list-type="picture" :headers="headerObj" :on-success="handleSuccess">
              <el-button size="small" type="primary">ÁÇπÂáª‰∏ä‰º†</el-button>
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="ÂïÜÂìÅÂÜÖÂÆπ" name="4">
            <!-- ÂØåÊñáÊú¨ÁºñËæëÂô®ÁªÑ‰ª∂ -->
            <quill-editor v-model="addForm.goods_introduce"></quill-editor>
            <!-- Ê∑ªÂä†ÂïÜÂìÅÁöÑÊåâÈíÆ -->
            <el-button type="primary" class="btnAdd" @click="add">Ê∑ªÂä†ÂïÜÂìÅ</el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>

    </el-card>

    <!-- ÂõæÁâáÈ¢ÑËßà -->
    <el-dialog title="ÂõæÁâáÈ¢ÑËßà" :visible.sync="previewVisible" width="50%">
      <img :src="previewPath" alt="" class="previewImg">
    </el-dialog>
  </div>
</template>

<script>
import _ from 'lodash'

export default {
  data() {
    return {
      activeIndex: '0',
      // Ê∑ªÂä†ÂïÜÂìÅÁöÑË°®ÂçïÊï∞ÊçÆÂØπË±°
      addForm: {
        goods_name: '',
        goods_price: 0,
        goods_weight: 0,
        goods_number: 0,
        // ÂïÜÂìÅÊâÄÂ±ûÁöÑÂàÜÁ±ªÊï∞ÁªÑ
        goods_cat: [],
        // ÂõæÁâáÁöÑÊï∞ÁªÑ
        pics: [],
        // ÂïÜÂìÅÁöÑËØ¶ÊÉÖÊèèËø∞
        goods_introduce: '',
        attrs: []
      },
      addFormRules: {
        goods_name: [
          { required: true, message: 'ËØ∑ËæìÂÖ•ÂïÜÂìÅÂêçÁß∞', trigger: 'blur' }
        ],
        goods_price: [
          { required: true, message: 'ËØ∑ËæìÂÖ•ÂïÜÂìÅ‰ª∑Ê†º', trigger: 'blur' }
        ],
        goods_weight: [
          { required: true, message: 'ËØ∑ËæìÂÖ•ÂïÜÂìÅÈáçÈáè', trigger: 'blur' }
        ],
        goods_number: [
          { required: true, message: 'ËØ∑ËæìÂÖ•ÂïÜÂìÅÊï∞Èáè', trigger: 'blur' }
        ],
        goods_cat: [
          { required: true, message: 'ËØ∑ÈÄâÊã©ÂïÜÂìÅÂàÜÁ±ª', trigger: 'blur' }
        ]
      },
      // ÂïÜÂìÅÂàÜÁ±ªÂàóË°®
      catelist: [],
      cateProps: {
        label: 'cat_name',
        value: 'cat_id',
        children: 'children'
      },
      // Âä®ÊÄÅÂèÇÊï∞ÂàóË°®Êï∞ÊçÆ
      manyTableData: [],
      // ÈùôÊÄÅÂ±ûÊÄßÂàóË°®Êï∞ÊçÆ
      onlyTableData: [],
      // ‰∏ä‰º†ÂõæÁâáÁöÑURLÂú∞ÂùÄ
      uploadURL: 'http://127.0.0.1:8888/api/private/v1/upload',
      // ÂõæÁâá‰∏ä‰º†ÁªÑ‰ª∂ÁöÑheadersËØ∑Ê±ÇÂ§¥ÂØπË±°
      headerObj: {
        Authorization: window.sessionStorage.getItem('token')
      },
      previewPath: '',
      previewVisible: false
    }
  },
  created() {
    this.getCateList()
  },
  methods: {
    // Ëé∑ÂèñÊâÄÊúâÂïÜÂìÅÂàÜÁ±ªÊï∞ÊçÆ
    async getCateList() {
      const { data: res } = await this.$http.get('categories')

      if (res.meta.status !== 200) {
        return this.$message.error('Ëé∑ÂèñÂïÜÂìÅÂàÜÁ±ªÊï∞ÊçÆÂ§±Ë¥•ÔºÅ')
      }

      this.catelist = res.data
      console.log(this.catelist)
    },
    // Á∫ßËÅîÈÄâÊã©Âô®ÈÄâ‰∏≠È°πÂèòÂåñÔºå‰ºöËß¶ÂèëËøô‰∏™ÂáΩÊï∞
    handleChange() {
      console.log(this.addForm.goods_cat)
      if (this.addForm.goods_cat.length !== 3) {
        this.addForm.goods_cat = []
      }
    },
    beforeTabLeave(activeName, oldActiveName) {
      // console.log('Âç≥Â∞ÜÁ¶ªÂºÄÁöÑÊ†áÁ≠æÈ°µÂêçÂ≠óÊòØÔºö' + oldActiveName)
      // console.log('Âç≥Â∞ÜËøõÂÖ•ÁöÑÊ†áÁ≠æÈ°µÂêçÂ≠óÊòØÔºö' + activeName)
      // return false
      if (oldActiveName === '0' && this.addForm.goods_cat.length !== 3) {
        this.$message.error('ËØ∑ÂÖàÈÄâÊã©ÂïÜÂìÅÂàÜÁ±ªÔºÅ')
        return false
      }
    },
    async tabClicked() {
      // console.log(this.activeIndex)
      // ËØÅÊòéËÆøÈóÆÁöÑÊòØÂä®ÊÄÅÂèÇÊï∞Èù¢Êùø
      if (this.activeIndex === '1') {
        const { data: res } = await this.$http.get(
          `categories/${this.cateId}/attributes`,
          {
            params: { sel: 'many' }
          }
        )

        if (res.meta.status !== 200) {
          return this.$message.error('Ëé∑ÂèñÂä®ÊÄÅÂèÇÊï∞ÂàóË°®Â§±Ë¥•ÔºÅ')
        }

        console.log(res.data)
        res.data.forEach(item => {
          item.attr_vals =
            item.attr_vals.length === 0 ? [] : item.attr_vals.split(' ')
        })
        this.manyTableData = res.data
      } else if (this.activeIndex === '2') {
        const { data: res } = await this.$http.get(
          `categories/${this.cateId}/attributes`,
          {
            params: { sel: 'only' }
          }
        )

        if (res.meta.status !== 200) {
          return this.$message.error('Ëé∑ÂèñÈùôÊÄÅÂ±ûÊÄßÂ§±Ë¥•ÔºÅ')
        }

        console.log(res.data)
        this.onlyTableData = res.data
      }
    },
    // Â§ÑÁêÜÂõæÁâáÈ¢ÑËßàÊïàÊûú
    handlePreview(file) {
      console.log(file)
      this.previewPath = file.response.data.url
      this.previewVisible = true
    },
    // Â§ÑÁêÜÁßªÈô§ÂõæÁâáÁöÑÊìç‰Ωú
    handleRemove(file) {
      // console.log(file)
      // 1. Ëé∑ÂèñÂ∞ÜË¶ÅÂà†Èô§ÁöÑÂõæÁâáÁöÑ‰∏¥Êó∂Ë∑ØÂæÑ
      const filePath = file.response.data.tmp_path
      // 2. ‰ªé pics Êï∞ÁªÑ‰∏≠ÔºåÊâæÂà∞Ëøô‰∏™ÂõæÁâáÂØπÂ∫îÁöÑÁ¥¢ÂºïÂÄº
      const i = this.addForm.pics.findIndex(x => x.pic === filePath)
      // 3. Ë∞ÉÁî®Êï∞ÁªÑÁöÑ splice ÊñπÊ≥ïÔºåÊääÂõæÁâá‰ø°ÊÅØÂØπË±°Ôºå‰ªé pics Êï∞ÁªÑ‰∏≠ÁßªÈô§
      this.addForm.pics.splice(i, 1)
      console.log(this.addForm)
    },
    // ÁõëÂê¨ÂõæÁâá‰∏ä‰º†ÊàêÂäüÁöÑ‰∫ã‰ª∂
    handleSuccess(response) {
      console.log(response)
      // 1. ÊãºÊé•ÂæóÂà∞‰∏Ä‰∏™ÂõæÁâá‰ø°ÊÅØÂØπË±°
      const picInfo = { pic: response.data.tmp_path }
      // 2. Â∞ÜÂõæÁâá‰ø°ÊÅØÂØπË±°Ôºåpush Âà∞picsÊï∞ÁªÑ‰∏≠
      this.addForm.pics.push(picInfo)
      console.log(this.addForm)
    },
    // Ê∑ªÂä†ÂïÜÂìÅ
    add() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) {
          return this.$message.error('ËØ∑Â°´ÂÜôÂøÖË¶ÅÁöÑË°®ÂçïÈ°πÔºÅ')
        }
        // ÊâßË°åÊ∑ªÂä†ÁöÑ‰∏öÂä°ÈÄªËæë
        // lodash   cloneDeep(obj)
        const form = _.cloneDeep(this.addForm)
        form.goods_cat = form.goods_cat.join(',')
        // Â§ÑÁêÜÂä®ÊÄÅÂèÇÊï∞
        this.manyTableData.forEach(item => {
          const newInfo = {
            attr_id: item.attr_id,
            attr_value: item.attr_vals.join(' ')
          }
          this.addForm.attrs.push(newInfo)
        })
        // Â§ÑÁêÜÈùôÊÄÅÂ±ûÊÄß
        this.onlyTableData.forEach(item => {
          const newInfo = { attr_id: item.attr_id, attr_value: item.attr_vals }
          this.addForm.attrs.push(newInfo)
        })
        form.attrs = this.addForm.attrs
        console.log(form)

        // ÂèëËµ∑ËØ∑Ê±ÇÊ∑ªÂä†ÂïÜÂìÅ
        // ÂïÜÂìÅÁöÑÂêçÁß∞ÔºåÂøÖÈ°ªÊòØÂîØ‰∏ÄÁöÑ
        const { data: res } = await this.$http.post('goods', form)

        if (res.meta.status !== 201) {
          return this.$message.error('Ê∑ªÂä†ÂïÜÂìÅÂ§±Ë¥•ÔºÅ')
        }

        this.$message.success('Ê∑ªÂä†ÂïÜÂìÅÊàêÂäüÔºÅ')
        this.$router.push('/goods')
      })
    }
  },
  computed: {
    cateId() {
      if (this.addForm.goods_cat.length === 3) {
        return this.addForm.goods_cat[2]
      }
      return null
    }
  }
}
</script>

<style lang="less" scoped>
.el-checkbox {
  margin: 0 10px 0 0 !important;
}

.previewImg {
  width: 100%;
}

.btnAdd {
  margin-top: 15px;
}
</style>
