<template>
  <div>
    <!-- Èù¢ÂåÖÂ±ëÂØºËà™Âå∫Âüü -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">È¶ñÈ°µ</el-breadcrumb-item>
      <el-breadcrumb-item>ÂïÜÂìÅÁÆ°ÁêÜ</el-breadcrumb-item>
      <el-breadcrumb-item>ÂïÜÂìÅÂàóË°®</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- Âç°ÁâáËßÜÂõæÂå∫Âüü -->
    <el-card>
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input placeholder="ËØ∑ËæìÂÖ•ÂÜÖÂÆπ" v-model="queryInfo.query" clearable @clear="getGoodsList">
            <el-button slot="append" icon="el-icon-search" @click="getGoodsList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="goAddpage">Ê∑ªÂä†ÂïÜÂìÅ</el-button>
        </el-col>
      </el-row>

      <!-- tableË°®Ê†ºÂå∫Âüü -->
      <el-table :data="goodslist" border stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column label="ÂïÜÂìÅÂêçÁß∞" prop="goods_name"></el-table-column>
        <el-table-column label="ÂïÜÂìÅ‰ª∑Ê†º(ÂÖÉ)" prop="goods_price" width="95px"></el-table-column>
        <el-table-column label="ÂïÜÂìÅÈáçÈáè" prop="goods_weight" width="70px"></el-table-column>
        <el-table-column label="ÂàõÂª∫Êó∂Èó¥" prop="add_time" width="140px">
          <template slot-scope="scope">
            {{scope.row.add_time | dateFormat}}
          </template>
        </el-table-column>
        <el-table-column label="Êìç‰Ωú" width="130px">
          <template slot-scope="scope">
            <el-button type="primary" icon="el-icon-edit" size="mini"></el-button>
            <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeById(scope.row.goods_id)"></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!-- ÂàÜÈ°µÂå∫Âüü -->
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="queryInfo.pagenum" :page-sizes="[5, 10, 15, 20]" :page-size="queryInfo.pagesize" layout="total, sizes, prev, pager, next, jumper" :total="total" background>
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // Êü•ËØ¢ÂèÇÊï∞ÂØπË±°
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10
      },
      // ÂïÜÂìÅÂàóË°®
      goodslist: [],
      // ÊÄªÊï∞ÊçÆÊù°Êï∞
      total: 0
    }
  },
  created() {
    this.getGoodsList()
  },
  methods: {
    // Ê†πÊçÆÂàÜÈ°µËé∑ÂèñÂØπÂ∫îÁöÑÂïÜÂìÅÂàóË°®
    async getGoodsList() {
      const { data: res } = await this.$http.get('goods', {
        params: this.queryInfo
      })

      if (res.meta.status !== 200) {
        return this.$message.error('Ëé∑ÂèñÂïÜÂìÅÂàóË°®Â§±Ë¥•ÔºÅ')
      }

      this.$message.success('Ëé∑ÂèñÂïÜÂìÅÂàóË°®ÊàêÂäüÔºÅ')
      console.log(res.data)
      this.goodslist = res.data.goods
      this.total = res.data.total
    },
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getGoodsList()
    },
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getGoodsList()
    },
    async removeById(id) {
      const confirmResult = await this.$confirm(
        'Ê≠§Êìç‰ΩúÂ∞ÜÊ∞∏‰πÖÂà†Èô§ËØ•ÂïÜÂìÅ, ÊòØÂê¶ÁªßÁª≠?',
        'ÊèêÁ§∫',
        {
          confirmButtonText: 'Á°ÆÂÆö',
          cancelButtonText: 'ÂèñÊ∂à',
          type: 'warning'
        }
      ).catch(err => err)

      if (confirmResult !== 'confirm') {
        return this.$message.info('Â∑≤ÁªèÂèñÊ∂àÂà†Èô§ÔºÅ')
      }

      const { data: res } = await this.$http.delete(`goods/${id}`)

      if (res.meta.status !== 200) {
        return this.$message.error('Âà†Èô§Â§±Ë¥•ÔºÅ')
      }

      this.$message.success('Âà†Èô§ÊàêÂäüÔºÅ')
      this.getGoodsList()
    },
    goAddpage() {
      this.$router.push('/goods/add')
    }
  }
}
</script>

<style lang="less" scoped>
</style>
