<template>
  <div>
    <!-- Èù¢ÂåÖÂ±ëÂØºËà™Âå∫Âüü -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">È¶ñÈ°µ</el-breadcrumb-item>
      <el-breadcrumb-item>ÂïÜÂìÅÁÆ°ÁêÜ</el-breadcrumb-item>
      <el-breadcrumb-item>ÂïÜÂìÅÂàÜÁ±ª</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- Âç°ÁâáËßÜÂõæÂå∫Âüü -->
    <el-card>
      <el-row>
        <el-col>
          <el-button type="primary" @click="showAddCateDialog">Ê∑ªÂä†ÂàÜÁ±ª</el-button>
        </el-col>
      </el-row>

      <!-- Ë°®Ê†º -->
      <tree-table class="treeTable" :data="catelist" :columns="columns" :selection-type="false" :expand-type="false" show-index index-text="#" border :show-row-hover="false">
        <!-- ÊòØÂê¶ÊúâÊïà -->
        <template slot="isok" slot-scope="scope">
          <i class="el-icon-success" v-if="scope.row.cat_deleted === false" style="color: lightgreen;"></i>
          <i class="el-icon-error" v-else style="color: red;"></i>
        </template>
        <!-- ÊéíÂ∫è -->
        <template slot="order" slot-scope="scope">
          <el-tag size="mini" v-if="scope.row.cat_level===0">‰∏ÄÁ∫ß</el-tag>
          <el-tag type="success" size="mini" v-else-if="scope.row.cat_level===1">‰∫åÁ∫ß</el-tag>
          <el-tag type="warning" size="mini" v-else>‰∏âÁ∫ß</el-tag>
        </template>
        <!-- Êìç‰Ωú -->
        <template slot="opt" slot-scope="scope">
          <el-button type="primary" icon="el-icon-edit" size="mini">ÁºñËæë</el-button>
          <el-button type="danger" icon="el-icon-delete" size="mini">Âà†Èô§</el-button>
        </template>
      </tree-table>

      <!-- ÂàÜÈ°µÂå∫Âüü -->
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="querInfo.pagenum" :page-sizes="[3, 5, 10, 15]" :page-size="querInfo.pagesize" layout="total, sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </el-card>

    <!-- Ê∑ªÂä†ÂàÜÁ±ªÁöÑÂØπËØùÊ°Ü -->
    <el-dialog title="Ê∑ªÂä†ÂàÜÁ±ª" :visible.sync="addCateDialogVisible" width="50%" @close="addCateDialogClosed">
      <!-- Ê∑ªÂä†ÂàÜÁ±ªÁöÑË°®Âçï -->
      <el-form :model="addCateForm" :rules="addCateFormRules" ref="addCateFormRef" label-width="100px">
        <el-form-item label="ÂàÜÁ±ªÂêçÁß∞Ôºö" prop="cat_name">
          <el-input v-model="addCateForm.cat_name"></el-input>
        </el-form-item>
        <el-form-item label="Áà∂Á∫ßÂàÜÁ±ªÔºö">
          <!-- options Áî®Êù•ÊåáÂÆöÊï∞ÊçÆÊ∫ê -->
          <!-- props Áî®Êù•ÊåáÂÆöÈÖçÁΩÆÂØπË±° -->
          <el-cascader expand-trigger="hover" :options="parentCateList" :props="cascaderProps" v-model="selectedKeys" @change="parentCateChanged" clearable change-on-select>
          </el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addCateDialogVisible = false">Âèñ Ê∂à</el-button>
        <el-button type="primary" @click="addCate">Á°Æ ÂÆö</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // Êü•ËØ¢Êù°‰ª∂
      querInfo: {
        type: 3,
        pagenum: 1,
        pagesize: 5
      },
      // ÂïÜÂìÅÂàÜÁ±ªÁöÑÊï∞ÊçÆÂàóË°®ÔºåÈªòËÆ§‰∏∫Á©∫
      catelist: [],
      // ÊÄªÊï∞ÊçÆÊù°Êï∞
      total: 0,
      // ‰∏∫tableÊåáÂÆöÂàóÁöÑÂÆö‰πâ
      columns: [
        {
          label: 'ÂàÜÁ±ªÂêçÁß∞',
          prop: 'cat_name'
        },
        {
          label: 'ÊòØÂê¶ÊúâÊïà',
          // Ë°®Á§∫ÔºåÂ∞ÜÂΩìÂâçÂàóÂÆö‰πâ‰∏∫Ê®°ÊùøÂàó
          type: 'template',
          // Ë°®Á§∫ÂΩìÂâçËøô‰∏ÄÂàó‰ΩøÁî®Ê®°ÊùøÂêçÁß∞
          template: 'isok'
        },
        {
          label: 'ÊéíÂ∫è',
          // Ë°®Á§∫ÔºåÂ∞ÜÂΩìÂâçÂàóÂÆö‰πâ‰∏∫Ê®°ÊùøÂàó
          type: 'template',
          // Ë°®Á§∫ÂΩìÂâçËøô‰∏ÄÂàó‰ΩøÁî®Ê®°ÊùøÂêçÁß∞
          template: 'order'
        },
        {
          label: 'Êìç‰Ωú',
          // Ë°®Á§∫ÔºåÂ∞ÜÂΩìÂâçÂàóÂÆö‰πâ‰∏∫Ê®°ÊùøÂàó
          type: 'template',
          // Ë°®Á§∫ÂΩìÂâçËøô‰∏ÄÂàó‰ΩøÁî®Ê®°ÊùøÂêçÁß∞
          template: 'opt'
        }
      ],
      // ÊéßÂà∂Ê∑ªÂä†ÂàÜÁ±ªÂØπËØùÊ°ÜÁöÑÊòæÁ§∫‰∏éÈöêËóè
      addCateDialogVisible: false,
      // Ê∑ªÂä†ÂàÜÁ±ªÁöÑË°®ÂçïÊï∞ÊçÆÂØπË±°
      addCateForm: {
        // Â∞ÜË¶ÅÊ∑ªÂä†ÁöÑÂàÜÁ±ªÁöÑÂêçÁß∞
        cat_name: '',
        // Áà∂Á∫ßÂàÜÁ±ªÁöÑId
        cat_pid: 0,
        // ÂàÜÁ±ªÁöÑÁ≠âÁ∫ßÔºåÈªòËÆ§Ë¶ÅÊ∑ªÂä†ÁöÑÊòØ1Á∫ßÂàÜÁ±ª
        cat_level: 0
      },
      // Ê∑ªÂä†ÂàÜÁ±ªË°®ÂçïÁöÑÈ™åËØÅËßÑÂàôÂØπË±°
      addCateFormRules: {
        cat_name: [
          { required: true, message: 'ËØ∑ËæìÂÖ•ÂàÜÁ±ªÂêçÁß∞', trigger: 'blur' }
        ]
      },
      // Áà∂Á∫ßÂàÜÁ±ªÁöÑÂàóË°®
      parentCateList: [],
      // ÊåáÂÆöÁ∫ßËÅîÈÄâÊã©Âô®ÁöÑÈÖçÁΩÆÂØπË±°
      cascaderProps: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      // ÈÄâ‰∏≠ÁöÑÁà∂Á∫ßÂàÜÁ±ªÁöÑIdÊï∞ÁªÑ
      selectedKeys: []
    }
  },
  created() {
    this.getCateList()
  },
  methods: {
    // Ëé∑ÂèñÂïÜÂìÅÂàÜÁ±ªÊï∞ÊçÆ
    async getCateList() {
      const { data: res } = await this.$http.get('categories', {
        params: this.querInfo
      })

      if (res.meta.status !== 200) {
        return this.$message.error('Ëé∑ÂèñÂïÜÂìÅÂàÜÁ±ªÂ§±Ë¥•ÔºÅ')
      }

      console.log(res.data)
      // ÊääÊï∞ÊçÆÂàóË°®ÔºåËµãÂÄºÁªô catelist
      this.catelist = res.data.result
      // ‰∏∫ÊÄªÊï∞ÊçÆÊù°Êï∞ËµãÂÄº
      this.total = res.data.total
    },
    // ÁõëÂê¨ pagesize ÊîπÂèò
    handleSizeChange(newSize) {
      this.querInfo.pagesize = newSize
      this.getCateList()
    },
    // ÁõëÂê¨ pagenum ÊîπÂèò
    handleCurrentChange(newPage) {
      this.querInfo.pagenum = newPage
      this.getCateList()
    },
    // ÁÇπÂáªÊåâÈíÆÔºåÂ±ïÁ§∫Ê∑ªÂä†ÂàÜÁ±ªÁöÑÂØπËØùÊ°Ü
    showAddCateDialog() {
      // ÂÖàËé∑ÂèñÁà∂Á∫ßÂàÜÁ±ªÁöÑÊï∞ÊçÆÂàóË°®
      this.getParentCateList()
      // ÂÜçÂ±ïÁ§∫Âá∫ÂØπËØùÊ°Ü
      this.addCateDialogVisible = true
    },
    // Ëé∑ÂèñÁà∂Á∫ßÂàÜÁ±ªÁöÑÊï∞ÊçÆÂàóË°®
    async getParentCateList() {
      const { data: res } = await this.$http.get('categories', {
        params: { type: 2 }
      })

      if (res.meta.status !== 200) {
        return this.$message.error('Ëé∑ÂèñÁà∂Á∫ßÂàÜÁ±ªÊï∞ÊçÆÂ§±Ë¥•ÔºÅ')
      }

      console.log(res.data)
      this.parentCateList = res.data
    },
    // ÈÄâÊã©È°πÂèëÁîüÂèòÂåñËß¶ÂèëËøô‰∏™ÂáΩÊï∞
    parentCateChanged() {
      console.log(this.selectedKeys)
      // Â¶ÇÊûú selectedKeys Êï∞ÁªÑ‰∏≠ÁöÑ length Â§ß‰∫é0ÔºåËØÅÊòéÈÄâ‰∏≠ÁöÑÁà∂Á∫ßÂàÜÁ±ª
      // Âèç‰πãÔºåÂ∞±ËØ¥ÊòéÊ≤°ÊúâÈÄâ‰∏≠‰ªª‰ΩïÁà∂Á∫ßÂàÜÁ±ª
      if (this.selectedKeys.length > 0) {
        // Áà∂Á∫ßÂàÜÁ±ªÁöÑId
        this.addCateForm.cat_pid = this.selectedKeys[
          this.selectedKeys.length - 1
        ]
        // ‰∏∫ÂΩìÂâçÂàÜÁ±ªÁöÑÁ≠âÁ∫ßËµãÂÄº
        this.addCateForm.cat_level = this.selectedKeys.length
      } else {
        // Áà∂Á∫ßÂàÜÁ±ªÁöÑId
        this.addCateForm.cat_pid = 0
        // ‰∏∫ÂΩìÂâçÂàÜÁ±ªÁöÑÁ≠âÁ∫ßËµãÂÄº
        this.addCateForm.cat_level = 0
      }
    },
    // ÁÇπÂáªÊåâÈíÆÔºåÊ∑ªÂä†Êñ∞ÁöÑÂàÜÁ±ª
    addCate() {
      this.$refs.addCateFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.post(
          'categories',
          this.addCateForm
        )

        if (res.meta.status !== 201) {
          return this.$message.error('Ê∑ªÂä†ÂàÜÁ±ªÂ§±Ë¥•ÔºÅ')
        }

        this.$message.success('Ê∑ªÂä†ÂàÜÁ±ªÊàêÂäüÔºÅ')
        this.getCateList()
        this.addCateDialogVisible = false
      })
    },
    // ÁõëÂê¨ÂØπËØùÊ°ÜÁöÑÂÖ≥Èó≠‰∫ã‰ª∂ÔºåÈáçÁΩÆË°®ÂçïÊï∞ÊçÆ
    addCateDialogClosed() {
      this.$refs.addCateFormRef.resetFields()
      this.selectedKeys = []
      this.addCateForm.cat_level = 0
      this.addCateForm.cat_pid = 0
    }
  }
}
</script>

<style lang="less" scoped>
.treeTable {
  margin-top: 15px;
}

.el-cascader {
  width: 100%;
}
</style>
