<template>
  <div>
    <!-- Èù¢ÂåÖÂ±ëÂØºËà™Âå∫Âüü -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">È¶ñÈ°µ</el-breadcrumb-item>
      <el-breadcrumb-item>ÂïÜÂìÅÁÆ°ÁêÜ</el-breadcrumb-item>
      <el-breadcrumb-item>ÂèÇÊï∞ÂàóË°®</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- Âç°ÁâáËßÜÂõæÂå∫Âüü -->
    <el-card>
      <!-- Ë≠¶ÂëäÂå∫Âüü -->
      <el-alert show-icon title="Ê≥®ÊÑèÔºöÂè™ÂÖÅËÆ∏‰∏∫Á¨¨‰∏âÁ∫ßÂàÜÁ±ªËÆæÁΩÆÁõ∏ÂÖ≥ÂèÇÊï∞ÔºÅ" type="warning" :closable="false"></el-alert>

      <!-- ÈÄâÊã©ÂïÜÂìÅÂàÜÁ±ªÂå∫Âüü -->
      <el-row class="cat_opt">
        <el-col>
          <span>ÈÄâÊã©ÂïÜÂìÅÂàÜÁ±ªÔºö</span>
          <!-- ÈÄâÊã©ÂïÜÂìÅÂàÜÁ±ªÁöÑÁ∫ßËÅîÈÄâÊã©Ê°Ü -->
          <el-cascader expand-trigger="hover" :options="catelist" :props="cateProps" v-model="selectedCateKeys" @change="handleChange">
          </el-cascader>
        </el-col>
      </el-row>

      <!-- tab È°µÁ≠æÂå∫Âüü -->
      <el-tabs v-model="activeName" @tab-click="handleTabClick">
        <!-- Ê∑ªÂä†Âä®ÊÄÅÂèÇÊï∞ÁöÑÈù¢Êùø -->
        <el-tab-pane label="Âä®ÊÄÅÂèÇÊï∞" name="many">
          <!-- Ê∑ªÂä†ÂèÇÊï∞ÁöÑÊåâÈíÆ -->
          <el-button type="primary" size="mini" :disabled="isBtnDisabled" @click="addDialogVisible=true">Ê∑ªÂä†ÂèÇÊï∞</el-button>
          <!-- Âä®ÊÄÅÂèÇÊï∞Ë°®Ê†º -->
          <el-table :data="manyTableData" border stripe>
            <!-- Â±ïÂºÄË°å -->
            <el-table-column type="expand">
              <template slot-scope="scope">
                <!-- Âæ™ÁéØÊ∏≤ÊüìTagÊ†áÁ≠æ -->
                <el-tag v-for="(item, i) in scope.row.attr_vals" :key="i" closable @close="handleClose(i, scope.row)">{{item}}</el-tag>
                <!-- ËæìÂÖ•ÁöÑÊñáÊú¨Ê°Ü -->
                <el-input class="input-new-tag" v-if="scope.row.inputVisible" v-model="scope.row.inputValue" ref="saveTagInput" size="small" @keyup.enter.native="handleInputConfirm(scope.row)" @blur="handleInputConfirm(scope.row)">
                </el-input>
                <!-- Ê∑ªÂä†ÊåâÈíÆ -->
                <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
              </template>
            </el-table-column>
            <!-- Á¥¢ÂºïÂàó -->
            <el-table-column type="index"></el-table-column>
            <el-table-column label="ÂèÇÊï∞ÂêçÁß∞" prop="attr_name"></el-table-column>
            <el-table-column label="Êìç‰Ωú">
              <template slot-scope="scope">
                <el-button size="mini" type="primary" icon="el-icon-edit" @click="showEditDialog(scope.row.attr_id)">ÁºñËæë</el-button>
                <el-button size="mini" type="danger" icon="el-icon-delete" @click="removeParams(scope.row.attr_id)">Âà†Èô§</el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <!-- Ê∑ªÂä†ÈùôÊÄÅÂ±ûÊÄßÁöÑÈù¢Êùø -->
        <el-tab-pane label="ÈùôÊÄÅÂ±ûÊÄß" name="only">
          <!-- Ê∑ªÂä†Â±ûÊÄßÁöÑÊåâÈíÆ -->
          <el-button type="primary" size="mini" :disabled="isBtnDisabled" @click="addDialogVisible=true">Ê∑ªÂä†Â±ûÊÄß</el-button>
          <!-- ÈùôÊÄÅÂ±ûÊÄßË°®Ê†º -->
          <el-table :data="onlyTableData" border stripe>
            <!-- Â±ïÂºÄË°å -->
            <el-table-column type="expand">
              <template slot-scope="scope">
                <!-- Âæ™ÁéØÊ∏≤ÊüìTagÊ†áÁ≠æ -->
                <el-tag v-for="(item, i) in scope.row.attr_vals" :key="i" closable @close="handleClose(i, scope.row)">{{item}}</el-tag>
                <!-- ËæìÂÖ•ÁöÑÊñáÊú¨Ê°Ü -->
                <el-input class="input-new-tag" v-if="scope.row.inputVisible" v-model="scope.row.inputValue" ref="saveTagInput" size="small" @keyup.enter.native="handleInputConfirm(scope.row)" @blur="handleInputConfirm(scope.row)">
                </el-input>
                <!-- Ê∑ªÂä†ÊåâÈíÆ -->
                <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
              </template>
            </el-table-column>
            <!-- Á¥¢ÂºïÂàó -->
            <el-table-column type="index"></el-table-column>
            <el-table-column label="Â±ûÊÄßÂêçÁß∞" prop="attr_name"></el-table-column>
            <el-table-column label="Êìç‰Ωú">
              <template slot-scope="scope">
                <el-button size="mini" type="primary" icon="el-icon-edit" @click="showEditDialog(scope.row.attr_id)">ÁºñËæë</el-button>
                <el-button size="mini" type="danger" icon="el-icon-delete" @click="removeParams(scope.row.attr_id)">Âà†Èô§</el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-card>

    <!-- Ê∑ªÂä†ÂèÇÊï∞ÁöÑÂØπËØùÊ°Ü -->
    <el-dialog :title="'Ê∑ªÂä†' + titleText" :visible.sync="addDialogVisible" width="50%" @close="addDialogClosed">
      <!-- Ê∑ªÂä†ÂèÇÊï∞ÁöÑÂØπËØùÊ°Ü -->
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px">
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="addForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false">Âèñ Ê∂à</el-button>
        <el-button type="primary" @click="addParams">Á°Æ ÂÆö</el-button>
      </span>
    </el-dialog>

    <!-- ‰øÆÊîπÂèÇÊï∞ÁöÑÂØπËØùÊ°Ü -->
    <el-dialog :title="'‰øÆÊîπ' + titleText" :visible.sync="editDialogVisible" width="50%" @close="editDialogClosed">
      <!-- Ê∑ªÂä†ÂèÇÊï∞ÁöÑÂØπËØùÊ°Ü -->
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="100px">
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="editForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">Âèñ Ê∂à</el-button>
        <el-button type="primary" @click="editParams">Á°Æ ÂÆö</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // ÂïÜÂìÅÂàÜÁ±ªÂàóË°®
      catelist: [],
      // Á∫ßËÅîÈÄâÊã©Ê°ÜÁöÑÈÖçÁΩÆÂØπË±°
      cateProps: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      // Á∫ßËÅîÈÄâÊã©Ê°ÜÂèåÂêëÁªëÂÆöÂà∞ÁöÑÊï∞ÁªÑ
      selectedCateKeys: [],
      // Ë¢´ÊøÄÊ¥ªÁöÑÈ°µÁ≠æÁöÑÂêçÁß∞
      activeName: 'many',
      // Âä®ÊÄÅÂèÇÊï∞ÁöÑÊï∞ÊçÆ
      manyTableData: [],
      // ÈùôÊÄÅÂ±ûÊÄßÁöÑÊï∞ÊçÆ
      onlyTableData: [],
      // ÊéßÂà∂Ê∑ªÂä†ÂØπËØùÊ°ÜÁöÑÊòæÁ§∫‰∏éÈöêËóè
      addDialogVisible: false,
      // Ê∑ªÂä†ÂèÇÊï∞ÁöÑË°®ÂçïÊï∞ÊçÆÂØπË±°
      addForm: {
        attr_name: ''
      },
      // Ê∑ªÂä†Ë°®ÂçïÁöÑÈ™åËØÅËßÑÂàôÂØπË±°
      addFormRules: {
        attr_name: [
          { required: true, message: 'ËØ∑ËæìÂÖ•ÂèÇÊï∞ÂêçÁß∞', trigger: 'blur' }
        ]
      },
      // ÊéßÂà∂‰øÆÊîπÂØπËØùÊ°ÜÁöÑÊòæÁ§∫‰∏éÈöêËóè
      editDialogVisible: false,
      // ‰øÆÊîπÁöÑË°®ÂçïÊï∞ÊçÆÂØπË±°
      editForm: {},
      // ‰øÆÊîπË°®ÂçïÁöÑÈ™åËØÅËßÑÂàôÂØπË±°
      editFormRules: {
        attr_name: [
          { required: true, message: 'ËØ∑ËæìÂÖ•ÂèÇÊï∞ÂêçÁß∞', trigger: 'blur' }
        ]
      }
    }
  },
  created() {
    this.getCateList()
  },
  methods: {
    // Ëé∑ÂèñÊâÄÊúâÁöÑÂïÜÂìÅÂàÜÁ±ªÂàóË°®
    async getCateList() {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) {
        return this.$message.error('Ëé∑ÂèñÂïÜÂìÅÂàÜÁ±ªÂ§±Ë¥•ÔºÅ')
      }

      this.catelist = res.data

      console.log(this.catelist)
    },
    // Á∫ßËÅîÈÄâÊã©Ê°ÜÈÄâ‰∏≠È°πÂèòÂåñÔºå‰ºöËß¶ÂèëËøô‰∏™ÂáΩÊï∞
    handleChange() {
      this.getParamsData()
    },
    // tab È°µÁ≠æÁÇπÂáª‰∫ã‰ª∂ÁöÑÂ§ÑÁêÜÂáΩÊï∞
    handleTabClick() {
      console.log(this.activeName)
      this.getParamsData()
    },
    // Ëé∑ÂèñÂèÇÊï∞ÁöÑÂàóË°®Êï∞ÊçÆ
    async getParamsData() {
      // ËØÅÊòéÈÄâ‰∏≠ÁöÑ‰∏çÊòØ‰∏âÁ∫ßÂàÜÁ±ª
      if (this.selectedCateKeys.length !== 3) {
        this.selectedCateKeys = []
        this.manyTableData = []
        this.onlyTableData = []
        return
      }

      // ËØÅÊòéÈÄâ‰∏≠ÁöÑÊòØ‰∏âÁ∫ßÂàÜÁ±ª
      console.log(this.selectedCateKeys)
      // Ê†πÊçÆÊâÄÈÄâÂàÜÁ±ªÁöÑIdÔºåÂíåÂΩìÂâçÊâÄÂ§ÑÁöÑÈù¢ÊùøÔºåËé∑ÂèñÂØπÂ∫îÁöÑÂèÇÊï∞
      const { data: res } = await this.$http.get(
        `categories/${this.cateId}/attributes`,
        {
          params: { sel: this.activeName }
        }
      )

      if (res.meta.status !== 200) {
        return this.$message.error('Ëé∑ÂèñÂèÇÊï∞ÂàóË°®Â§±Ë¥•ÔºÅ')
      }

      res.data.forEach(item => {
        item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
        // ÊéßÂà∂ÊñáÊú¨Ê°ÜÁöÑÊòæÁ§∫‰∏éÈöêËóè
        item.inputVisible = false
        // ÊñáÊú¨Ê°Ü‰∏≠ËæìÂÖ•ÁöÑÂÄº
        item.inputValue = ''
      })

      console.log(res.data)
      if (this.activeName === 'many') {
        this.manyTableData = res.data
      } else {
        this.onlyTableData = res.data
      }
    },
    // ÁõëÂê¨Ê∑ªÂä†ÂØπËØùÊ°ÜÁöÑÂÖ≥Èó≠‰∫ã‰ª∂
    addDialogClosed() {
      this.$refs.addFormRef.resetFields()
    },
    // ÁÇπÂáªÊåâÈíÆÔºåÊ∑ªÂä†ÂèÇÊï∞
    addParams() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.post(
          `categories/${this.cateId}/attributes`,
          {
            attr_name: this.addForm.attr_name,
            attr_sel: this.activeName
          }
        )

        if (res.meta.status !== 201) {
          return this.$message.error('Ê∑ªÂä†ÂèÇÊï∞Â§±Ë¥•ÔºÅ')
        }

        this.$message.success('Ê∑ªÂä†ÂèÇÊï∞ÊàêÂäüÔºÅ')
        this.addDialogVisible = false
        this.getParamsData()
      })
    },
    // ÁÇπÂáªÊåâÈíÆÔºåÂ±ïÁ§∫‰øÆÊîπÁöÑÂØπËØùÊ°Ü
    async showEditDialog(attr_id) {
      // Êü•ËØ¢ÂΩìÂâçÂèÇÊï∞ÁöÑ‰ø°ÊÅØ
      const { data: res } = await this.$http.get(
        `categories/${this.cateId}/attributes/${attr_id}`,
        {
          params: { attr_sel: this.activeName }
        }
      )

      if (res.meta.status !== 200) {
        return this.$message.error('Ëé∑ÂèñÂèÇÊï∞‰ø°ÊÅØÂ§±Ë¥•ÔºÅ')
      }

      this.editForm = res.data
      this.editDialogVisible = true
    },
    // ÈáçÁΩÆ‰øÆÊîπÁöÑË°®Âçï
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },
    // ÁÇπÂáªÊåâÈíÆÔºå‰øÆÊîπÂèÇÊï∞‰ø°ÊÅØ
    editParams() {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.put(
          `categories/${this.cateId}/attributes/${this.editForm.attr_id}`,
          { attr_name: this.editForm.attr_name, attr_sel: this.activeName }
        )

        if (res.meta.status !== 200) {
          return this.$message.error('‰øÆÊîπÂèÇÊï∞Â§±Ë¥•ÔºÅ')
        }

        this.$message.success('‰øÆÊîπÂèÇÊï∞ÊàêÂäüÔºÅ')
        this.getParamsData()
        this.editDialogVisible = false
      })
    },
    // Ê†πÊçÆIdÂà†Èô§ÂØπÂ∫îÁöÑÂèÇÊï∞È°π
    async removeParams(attr_id) {
      const confirmResult = await this.$confirm(
        'Ê≠§Êìç‰ΩúÂ∞ÜÊ∞∏‰πÖÂà†Èô§ËØ•ÂèÇÊï∞, ÊòØÂê¶ÁªßÁª≠?',
        'ÊèêÁ§∫',
        {
          confirmButtonText: 'Á°ÆÂÆö',
          cancelButtonText: 'ÂèñÊ∂à',
          type: 'warning'
        }
      ).catch(err => err)

      // Áî®Êà∑ÂèñÊ∂à‰∫ÜÂà†Èô§ÁöÑÊìç‰Ωú
      if (confirmResult !== 'confirm') {
        return this.$message.info('Â∑≤ÂèñÊ∂àÂà†Èô§ÔºÅ')
      }

      // Âà†Èô§ÁöÑ‰∏öÂä°ÈÄªËæë
      const { data: res } = await this.$http.delete(
        `categories/${this.cateId}/attributes/${attr_id}`
      )

      if (res.meta.status !== 200) {
        return this.$message.error('Âà†Èô§ÂèÇÊï∞Â§±Ë¥•ÔºÅ')
      }

      this.$message.success('Âà†Èô§ÂèÇÊï∞ÊàêÂäüÔºÅ')
      this.getParamsData()
    },
    // ÊñáÊú¨Ê°ÜÂ§±ÂéªÁÑ¶ÁÇπÔºåÊàñÊëÅ‰∏ã‰∫Ü Enter ÈÉΩ‰ºöËß¶Âèë
    async handleInputConfirm(row) {
      if (row.inputValue.trim().length === 0) {
        row.inputValue = ''
        row.inputVisible = false
        return
      }
      // Â¶ÇÊûúÊ≤°ÊúâreturnÔºåÂàôËØÅÊòéËæìÂÖ•ÁöÑÂÜÖÂÆπÔºåÈúÄË¶ÅÂÅöÂêéÁª≠Â§ÑÁêÜ
      row.attr_vals.push(row.inputValue.trim())
      row.inputValue = ''
      row.inputVisible = false
      // ÈúÄË¶ÅÂèëËµ∑ËØ∑Ê±ÇÔºå‰øùÂ≠òËøôÊ¨°Êìç‰Ωú
      this.saveAttrVals(row)
    },
    // Â∞ÜÂØπ attr_vals ÁöÑÊìç‰ΩúÔºå‰øùÂ≠òÂà∞Êï∞ÊçÆÂ∫ì
    async saveAttrVals(row) {
      // ÈúÄË¶ÅÂèëËµ∑ËØ∑Ê±ÇÔºå‰øùÂ≠òËøôÊ¨°Êìç‰Ωú
      const { data: res } = await this.$http.put(
        `categories/${this.cateId}/attributes/${row.attr_id}`,
        {
          attr_name: row.attr_name,
          attr_sel: row.attr_sel,
          attr_vals: row.attr_vals.join(' ')
        }
      )

      if (res.meta.status !== 200) {
        return this.$message.error('‰øÆÊîπÂèÇÊï∞È°πÂ§±Ë¥•ÔºÅ')
      }

      this.$message.success('‰øÆÊîπÂèÇÊï∞È°πÊàêÂäüÔºÅ')
    },
    // ÁÇπÂáªÊåâÈíÆÔºåÂ±ïÁ§∫ÊñáÊú¨ËæìÂÖ•Ê°Ü
    showInput(row) {
      row.inputVisible = true
      // ËÆ©ÊñáÊú¨Ê°ÜËá™Âä®Ëé∑ÂæóÁÑ¶ÁÇπ
      // $nextTick ÊñπÊ≥ïÁöÑ‰ΩúÁî®ÔºåÂ∞±ÊòØÂΩìÈ°µÈù¢‰∏äÂÖÉÁ¥†Ë¢´ÈáçÊñ∞Ê∏≤Êüì‰πãÂêéÔºåÊâç‰ºöÊåáÂÆöÂõûË∞ÉÂáΩÊï∞‰∏≠ÁöÑ‰ª£Á†Å
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },
    // Âà†Èô§ÂØπÂ∫îÁöÑÂèÇÊï∞ÂèØÈÄâÈ°π
    handleClose(i, row) {
      row.attr_vals.splice(i, 1)
      this.saveAttrVals(row)
    }
  },
  computed: {
    // Â¶ÇÊûúÊåâÈíÆÈúÄË¶ÅË¢´Á¶ÅÁî®ÔºåÂàôËøîÂõûtrueÔºåÂê¶ÂàôËøîÂõûfalse
    isBtnDisabled() {
      if (this.selectedCateKeys.length !== 3) {
        return true
      }
      return false
    },
    // ÂΩìÂâçÈÄâ‰∏≠ÁöÑ‰∏âÁ∫ßÂàÜÁ±ªÁöÑId
    cateId() {
      if (this.selectedCateKeys.length === 3) {
        return this.selectedCateKeys[2]
      }
      return null
    },
    // Âä®ÊÄÅËÆ°ÁÆóÊ†áÈ¢òÁöÑÊñáÊú¨
    titleText() {
      if (this.activeName === 'many') {
        return 'Âä®ÊÄÅÂèÇÊï∞'
      }
      return 'ÈùôÊÄÅÂ±ûÊÄß'
    }
  }
}
</script>

<style lang="less" scoped>
.cat_opt {
  margin: 15px 0;
}

.el-tag {
  margin: 10px;
}

.input-new-tag {
  width: 120px;
}
</style>
