<script>
import cityData from './citydata.js'

export default {
  data() {
    return {
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10
      },
      total: 0,
      orderlist: [],
      addressVisible: false,
      addressForm: {
        address1: [],
        address2: ''
      },
      addressFormRules: {
        address1: [
          { required: true, message: 'ËØ∑ÈÄâÊã©ÁúÅÂ∏ÇÂå∫Âéø', trigger: 'blur' }
        ],
        address2: [
          { required: true, message: 'ËØ∑Â°´ÂÜôËØ¶ÁªÜÂú∞ÂùÄ', trigger: 'blur' }
        ]
      },
      cityData,
      progressVisible: false,
      progressInfo: []
    }
  },
  created() {
    this.getOrderList()
  },
  methods: {
    async getOrderList() {
      const { data: res } = await this.$http.get('orders', {
        params: this.queryInfo
      })

      if (res.meta.status !== 200) {
        return this.$message.error('Ëé∑ÂèñËÆ¢ÂçïÂàóË°®Â§±Ë¥•ÔºÅ')
      }

      console.log(res)
      this.total = res.data.total
      this.orderlist = res.data.goods
    },
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getOrderList()
    },
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getOrderList()
    },
    // Â±ïÁ§∫‰øÆÊîπÂú∞ÂùÄÁöÑÂØπËØùÊ°Ü
    showBox() {
      this.addressVisible = true
    },
    addressDialogClosed() {
      this.$refs.addressFormRef.resetFields()
    },
    async showProgressBox() {
      const { data: res } = await this.$http.get('/kuaidi/804909574412544580')

      if (res.meta.status !== 200) {
        return this.$message.error('Ëé∑ÂèñÁâ©ÊµÅËøõÂ∫¶Â§±Ë¥•ÔºÅ')
      }

      this.progressInfo = res.data

      this.progressVisible = true
      console.log(this.progressInfo)
    }
  }
}
</script>

