<template>
  <div>
    <!-- Èù¢ÂåÖÂ±ëÂØºËà™Âå∫Âüü -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">È¶ñÈ°µ</el-breadcrumb-item>
      <el-breadcrumb-item>‰∏ªÈ°µ</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- Âç°ÁâáËßÜÂõæÂå∫Âüü -->
    <el-card>
      <!-- ÊêúÁ¥¢‰∏éÊ∑ªÂä†Âå∫Âüü -->
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input placeholder="ËØ∑ËæìÂÖ•ÂÜÖÂÆπ" v-model="queryInfo.query" clearable @clear="getUserList">
            <el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible = true">Ê∑ªÂä†Áî®Êà∑</el-button>
        </el-col>
      </el-row>

      <!-- Áî®Êà∑ÂàóË°®Âå∫Âüü -->
      <el-table :data="userlist" border stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column label="ÂßìÂêç" prop="username"></el-table-column>
        <el-table-column label="ÈÇÆÁÆ±" prop="email"></el-table-column>
        <el-table-column label="ÁîµËØù" prop="mobile"></el-table-column>
        <el-table-column label="ËßíËâ≤" prop="role_name"></el-table-column>
        <el-table-column label="Áä∂ÊÄÅ">
          <template slot-scope="scope">
            <el-switch v-model="scope.row.mg_state" @change="userStateChanged(scope.row)">
            </el-switch>
          </template>
        </el-table-column>
        <el-table-column label="Êìç‰Ωú" width="180px">
          <template slot-scope="scope">
            <!-- ‰øÆÊîπÊåâÈíÆ -->
            <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.id)"></el-button>
            <!-- Âà†Èô§ÊåâÈíÆ -->
            <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeUserById(scope.row.id)"></el-button>
            <!-- ÂàÜÈÖçËßíËâ≤ÊåâÈíÆ -->
            <el-tooltip effect="dark" content="ÂàÜÈÖçËßíËâ≤" placement="top" :enterable="false">
              <el-button type="warning" icon="el-icon-setting" size="mini" @click="setRole(scope.row)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!-- ÂàÜÈ°µÂå∫Âüü -->
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="queryInfo.pagenum" :page-sizes="[1, 2, 5, 10]" :page-size="queryInfo.pagesize" layout="total, sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </el-card>

    <!-- Ê∑ªÂä†Áî®Êà∑ÁöÑÂØπËØùÊ°Ü -->
    <el-dialog title="Ê∑ªÂä†Áî®Êà∑" :visible.sync="addDialogVisible" width="50%" @close="addDialogClosed">
      <!-- ÂÜÖÂÆπ‰∏ª‰ΩìÂå∫Âüü -->
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="70px">
        <el-form-item label="Áî®Êà∑Âêç" prop="username">
          <el-input v-model="addForm.username"></el-input>
        </el-form-item>
        <el-form-item label="ÂØÜÁ†Å" prop="password">
          <el-input v-model="addForm.password"></el-input>
        </el-form-item>
        <el-form-item label="ÈÇÆÁÆ±" prop="email">
          <el-input v-model="addForm.email"></el-input>
        </el-form-item>
        <el-form-item label="ÊâãÊú∫" prop="mobile">
          <el-input v-model="addForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <!-- Â∫ïÈÉ®Âå∫Âüü -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false">Âèñ Ê∂à</el-button>
        <el-button type="primary" @click="addUser">Á°Æ ÂÆö</el-button>
      </span>
    </el-dialog>

    <!-- ‰øÆÊîπÁî®Êà∑ÁöÑÂØπËØùÊ°Ü -->
    <el-dialog title="‰øÆÊîπÁî®Êà∑" :visible.sync="editDialogVisible" width="50%" @close="editDialogClosed">
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="70px">
        <el-form-item label="Áî®Êà∑Âêç">
          <el-input v-model="editForm.username" disabled></el-input>
        </el-form-item>
        <el-form-item label="ÈÇÆÁÆ±" prop="email">
          <el-input v-model="editForm.email"></el-input>
        </el-form-item>
        <el-form-item label="ÊâãÊú∫" prop="mobile">
          <el-input v-model="editForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">Âèñ Ê∂à</el-button>
        <el-button type="primary" @click="editUserInfo">Á°Æ ÂÆö</el-button>
      </span>
    </el-dialog>

    <!-- ÂàÜÈÖçËßíËâ≤ÁöÑÂØπËØùÊ°Ü -->
    <el-dialog title="ÂàÜÈÖçËßíËâ≤" :visible.sync="setRoleDialogVisible" width="50%" @close="setRoleDialogClosed">
      <div>
        <p>ÂΩìÂâçÁöÑÁî®Êà∑Ôºö{{userInfo.username}}</p>
        <p>ÂΩìÂâçÁöÑËßíËâ≤Ôºö{{userInfo.role_name}}</p>
        <p>ÂàÜÈÖçÊñ∞ËßíËâ≤Ôºö
          <el-select v-model="selectedRoleId" placeholder="ËØ∑ÈÄâÊã©">
            <el-option v-for="item in rolesList" :key="item.id" :label="item.roleName" :value="item.id">
            </el-option>
          </el-select>
        </p>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRoleDialogVisible = false">Âèñ Ê∂à</el-button>
        <el-button type="primary" @click="saveRoleInfo">Á°Æ ÂÆö</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    // È™åËØÅÈÇÆÁÆ±ÁöÑËßÑÂàô
    var checkEmail = (rule, value, cb) => {
      // È™åËØÅÈÇÆÁÆ±ÁöÑÊ≠£ÂàôË°®ËææÂºè
      const regEmail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/

      if (regEmail.test(value)) {
        // ÂêàÊ≥ïÁöÑÈÇÆÁÆ±
        return cb()
      }

      cb(new Error('ËØ∑ËæìÂÖ•ÂêàÊ≥ïÁöÑÈÇÆÁÆ±'))
    }

    // È™åËØÅÊâãÊú∫Âè∑ÁöÑËßÑÂàô
    var checkMobile = (rule, value, cb) => {
      // È™åËØÅÊâãÊú∫Âè∑ÁöÑÊ≠£ÂàôË°®ËææÂºè
      const regMobile = /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/

      if (regMobile.test(value)) {
        return cb()
      }

      cb(new Error('ËØ∑ËæìÂÖ•ÂêàÊ≥ïÁöÑÊâãÊú∫Âè∑'))
    }

    return {
      // Ëé∑ÂèñÁî®Êà∑ÂàóË°®ÁöÑÂèÇÊï∞ÂØπË±°
      queryInfo: {
        query: '',
        // ÂΩìÂâçÁöÑÈ°µÊï∞
        pagenum: 1,
        // ÂΩìÂâçÊØèÈ°µÊòæÁ§∫Â§öÂ∞ëÊù°Êï∞ÊçÆ
        pagesize: 2
      },
      userlist: [],
      total: 0,
      // ÊéßÂà∂Ê∑ªÂä†Áî®Êà∑ÂØπËØùÊ°ÜÁöÑÊòæÁ§∫‰∏éÈöêËóè
      addDialogVisible: false,
      // Ê∑ªÂä†Áî®Êà∑ÁöÑË°®ÂçïÊï∞ÊçÆ
      addForm: {
        username: '',
        password: '',
        email: '',
        mobile: ''
      },
      // Ê∑ªÂä†Ë°®ÂçïÁöÑÈ™åËØÅËßÑÂàôÂØπË±°
      addFormRules: {
        username: [
          { required: true, message: 'ËØ∑ËæìÂÖ•Áî®Êà∑Âêç', trigger: 'blur' },
          {
            min: 3,
            max: 10,
            message: 'Áî®Êà∑ÂêçÁöÑÈïøÂ∫¶Âú®3~10‰∏™Â≠óÁ¨¶‰πãÈó¥',
            trigger: 'blur'
          }
        ],
        password: [
          { required: true, message: 'ËØ∑ËæìÂÖ•ÂØÜÁ†Å', trigger: 'blur' },
          {
            min: 6,
            max: 15,
            message: 'Áî®Êà∑ÂêçÁöÑÈïøÂ∫¶Âú®6~15‰∏™Â≠óÁ¨¶‰πãÈó¥',
            trigger: 'blur'
          }
        ],
        email: [
          { required: true, message: 'ËØ∑ËæìÂÖ•ÈÇÆÁÆ±', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' }
        ],
        mobile: [
          { required: true, message: 'ËØ∑ËæìÂÖ•ÊâãÊú∫Âè∑', trigger: 'blur' },
          { validator: checkMobile, trigger: 'blur' }
        ]
      },
      // ÊéßÂà∂‰øÆÊîπÁî®Êà∑ÂØπËØùÊ°ÜÁöÑÊòæÁ§∫‰∏éÈöêËóè
      editDialogVisible: false,
      // Êü•ËØ¢Âà∞ÁöÑÁî®Êà∑‰ø°ÊÅØÂØπË±°
      editForm: {},
      // ‰øÆÊîπË°®ÂçïÁöÑÈ™åËØÅËßÑÂàôÂØπË±°
      editFormRules: {
        email: [
          { required: true, message: 'ËØ∑ËæìÂÖ•Áî®Êà∑ÈÇÆÁÆ±', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' }
        ],
        mobile: [
          { required: true, message: 'ËØ∑ËæìÂÖ•Áî®Êà∑ÊâãÊú∫', trigger: 'blur' },
          { validator: checkMobile, trigger: 'blur' }
        ]
      },
      // ÊéßÂà∂ÂàÜÈÖçËßíËâ≤ÂØπËØùÊ°ÜÁöÑÊòæÁ§∫‰∏éÈöêËóè
      setRoleDialogVisible: false,
      // ÈúÄË¶ÅË¢´ÂàÜÈÖçËßíËâ≤ÁöÑÁî®Êà∑‰ø°ÊÅØ
      userInfo: {},
      // ÊâÄÊúâËßíËâ≤ÁöÑÊï∞ÊçÆÂàóË°®
      rolesList: [],
      // Â∑≤ÈÄâ‰∏≠ÁöÑËßíËâ≤IdÂÄº
      selectedRoleId: ''
    }
  },
  created() {
    this.getUserList()
  },
  methods: {
    async getUserList() {
      const { data: res } = await this.$http.get('users', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) {
        return this.$message.error('Ëé∑ÂèñÁî®Êà∑ÂàóË°®Â§±Ë¥•ÔºÅ')
      }
      this.userlist = res.data.users
      this.total = res.data.total
      console.log(res)
    },
    // ÁõëÂê¨ pagesize ÊîπÂèòÁöÑ‰∫ã‰ª∂
    handleSizeChange(newSize) {
      // console.log(newSize)
      this.queryInfo.pagesize = newSize
      this.getUserList()
    },
    // ÁõëÂê¨ È°µÁ†ÅÂÄº ÊîπÂèòÁöÑ‰∫ã‰ª∂
    handleCurrentChange(newPage) {
      console.log(newPage)
      this.queryInfo.pagenum = newPage
      this.getUserList()
    },
    // ÁõëÂê¨ switch ÂºÄÂÖ≥Áä∂ÊÄÅÁöÑÊîπÂèò
    async userStateChanged(userinfo) {
      console.log(userinfo)
      const { data: res } = await this.$http.put(
        `users/${userinfo.id}/state/${userinfo.mg_state}`
      )
      if (res.meta.status !== 200) {
        userinfo.mg_state = !userinfo.mg_state
        return this.$message.error('Êõ¥Êñ∞Áî®Êà∑Áä∂ÊÄÅÂ§±Ë¥•ÔºÅ')
      }
      this.$message.success('Êõ¥Êñ∞Áî®Êà∑Áä∂ÊÄÅÊàêÂäüÔºÅ')
    },
    // ÁõëÂê¨Ê∑ªÂä†Áî®Êà∑ÂØπËØùÊ°ÜÁöÑÂÖ≥Èó≠‰∫ã‰ª∂
    addDialogClosed() {
      this.$refs.addFormRef.resetFields()
    },
    // ÁÇπÂáªÊåâÈíÆÔºåÊ∑ªÂä†Êñ∞Áî®Êà∑
    addUser() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return
        // ÂèØ‰ª•ÂèëËµ∑Ê∑ªÂä†Áî®Êà∑ÁöÑÁΩëÁªúËØ∑Ê±Ç
        const { data: res } = await this.$http.post('users', this.addForm)

        if (res.meta.status !== 201) {
          this.$message.error('Ê∑ªÂä†Áî®Êà∑Â§±Ë¥•ÔºÅ')
        }

        this.$message.success('Ê∑ªÂä†Áî®Êà∑ÊàêÂäüÔºÅ')
        // ÈöêËóèÊ∑ªÂä†Áî®Êà∑ÁöÑÂØπËØùÊ°Ü
        this.addDialogVisible = false
        // ÈáçÊñ∞Ëé∑ÂèñÁî®Êà∑ÂàóË°®Êï∞ÊçÆ
        this.getUserList()
      })
    },
    // Â±ïÁ§∫ÁºñËæëÁî®Êà∑ÁöÑÂØπËØùÊ°Ü
    async showEditDialog(id) {
      // console.log(id)
      const { data: res } = await this.$http.get('users/' + id)

      if (res.meta.status !== 200) {
        return this.$message.error('Êü•ËØ¢Áî®Êà∑‰ø°ÊÅØÂ§±Ë¥•ÔºÅ')
      }

      this.editForm = res.data
      this.editDialogVisible = true
    },
    // ÁõëÂê¨‰øÆÊîπÁî®Êà∑ÂØπËØùÊ°ÜÁöÑÂÖ≥Èó≠‰∫ã‰ª∂
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },
    // ‰øÆÊîπÁî®Êà∑‰ø°ÊÅØÂπ∂Êèê‰∫§
    editUserInfo() {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return
        // ÂèëËµ∑‰øÆÊîπÁî®Êà∑‰ø°ÊÅØÁöÑÊï∞ÊçÆËØ∑Ê±Ç
        const { data: res } = await this.$http.put(
          'users/' + this.editForm.id,
          {
            email: this.editForm.email,
            mobile: this.editForm.mobile
          }
        )

        if (res.meta.status !== 200) {
          return this.$message.error('Êõ¥Êñ∞Áî®Êà∑‰ø°ÊÅØÂ§±Ë¥•ÔºÅ')
        }

        // ÂÖ≥Èó≠ÂØπËØùÊ°Ü
        this.editDialogVisible = false
        // Âà∑Êñ∞Êï∞ÊçÆÂàóË°®
        this.getUserList()
        // ÊèêÁ§∫‰øÆÊîπÊàêÂäü
        this.$message.success('Êõ¥Êñ∞Áî®Êà∑‰ø°ÊÅØÊàêÂäüÔºÅ')
      })
    },
    // Ê†πÊçÆIdÂà†Èô§ÂØπÂ∫îÁöÑÁî®Êà∑‰ø°ÊÅØ
    async removeUserById(id) {
      // ÂºπÊ°ÜËØ¢ÈóÆÁî®Êà∑ÊòØÂê¶Âà†Èô§Êï∞ÊçÆ
      const confirmResult = await this.$confirm(
        'Ê≠§Êìç‰ΩúÂ∞ÜÊ∞∏‰πÖÂà†Èô§ËØ•Áî®Êà∑, ÊòØÂê¶ÁªßÁª≠?',
        'ÊèêÁ§∫',
        {
          confirmButtonText: 'Á°ÆÂÆö',
          cancelButtonText: 'ÂèñÊ∂à',
          type: 'warning'
        }
      ).catch(err => err)

      // Â¶ÇÊûúÁî®Êà∑Á°ÆËÆ§Âà†Èô§ÔºåÂàôËøîÂõûÂÄº‰∏∫Â≠óÁ¨¶‰∏≤ confirm
      // Â¶ÇÊûúÁî®Êà∑ÂèñÊ∂à‰∫ÜÂà†Èô§ÔºåÂàôËøîÂõûÂÄº‰∏∫Â≠óÁ¨¶‰∏≤ cancel
      // console.log(confirmResult)
      if (confirmResult !== 'confirm') {
        return this.$message.info('Â∑≤ÂèñÊ∂àÂà†Èô§')
      }

      const { data: res } = await this.$http.delete('users/' + id)

      if (res.meta.status !== 200) {
        return this.$message.error('Âà†Èô§Áî®Êà∑Â§±Ë¥•ÔºÅ')
      }

      this.$message.success('Âà†Èô§Áî®Êà∑ÊàêÂäüÔºÅ')
      this.getUserList()
    },
    // Â±ïÁ§∫ÂàÜÈÖçËßíËâ≤ÁöÑÂØπËØùÊ°Ü
    async setRole(userInfo) {
      this.userInfo = userInfo

      // Âú®Â±ïÁ§∫ÂØπËØùÊ°Ü‰πãÂâçÔºåËé∑ÂèñÊâÄÊúâËßíËâ≤ÁöÑÂàóË°®
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200) {
        return this.$message.error('Ëé∑ÂèñËßíËâ≤ÂàóË°®Â§±Ë¥•ÔºÅ')
      }

      this.rolesList = res.data

      this.setRoleDialogVisible = true
    },
    // ÁÇπÂáªÊåâÈíÆÔºåÂàÜÈÖçËßíËâ≤
    async saveRoleInfo() {
      if (!this.selectedRoleId) {
        return this.$message.error('ËØ∑ÈÄâÊã©Ë¶ÅÂàÜÈÖçÁöÑËßíËâ≤ÔºÅ')
      }

      const { data: res } = await this.$http.put(
        `users/${this.userInfo.id}/role`,
        {
          rid: this.selectedRoleId
        }
      )

      if (res.meta.status !== 200) {
        return this.$message.error('Êõ¥Êñ∞ËßíËâ≤Â§±Ë¥•ÔºÅ')
      }

      this.$message.success('Êõ¥Êñ∞ËßíËâ≤ÊàêÂäüÔºÅ')
      this.getUserList()
      this.setRoleDialogVisible = false
    },
    // ÁõëÂê¨ÂàÜÈÖçËßíËâ≤ÂØπËØùÊ°ÜÁöÑÂÖ≥Èó≠‰∫ã‰ª∂
    setRoleDialogClosed() {
      this.selectedRoleId = ''
      this.userInfo = {}
    }
  }
}
</script>

<style lang="less" scoped>
</style>
